import React, { createContext, useState, useRef } from 'react';

export const AppContext = createContext();

export const AppProvider = ({ children }) => {
    // Estado para almacenar el servicio seleccionado que se mostrar en Home
    const [activeTopicId, setActiveTopicId] = useState(null);

    // Estado para controlar si se ha hecho scroll
    const [hasScrolled, setHasScrolled] = useState(false);

    // Estado para controlar si se debe mostrar el detalle en Home
    const [showTopicDetail, setShowTopicDetail] = useState(false);

    // Nuevo estado para manejar la pestaa activa
    const [activeTabIndex, setActiveTabIndex] = useState(0);

    // Referencia para controlar si hay una transicin en curso
    const isTransitioning = useRef(false);

    // Funcin para activar un topic especfico en Home
    const activateTopic = (topicId, tabIndex = 0) => {
        setActiveTopicId(topicId);
        setActiveTabIndex(tabIndex); // Establecer la pestaa activa
        setShowTopicDetail(true);
        setHasScrolled(true);
    };

    // Funcin para desactivar la visualizacin del topic
    // Esta funcin ahora solo cambia el estado showTopicDetail
    // La lgica de transicin se maneja en el componente Home
    const deactivateTopic = () => {
        if (isTransitioning.current) return;

        isTransitioning.current = true;

        // Primero cambiamos el showTopicDetail para iniciar la animacin en Home
        setShowTopicDetail(false);

        // Despus de que termine la transicin (2 segundos), limpiamos el activeTopicId
        setTimeout(() => {
            // Reestablecemos el estado de transicin
            isTransitioning.current = false;
            setActiveTabIndex(0); // Resetear la pestaa activa
        }, 2000);
    };

    // Valores y funciones que queremos compartir entre componentes
    const contextValue = {
        activeTopicId,
        hasScrolled,
        showTopicDetail,
        activeTabIndex,
        setHasScrolled,
        setActiveTabIndex,
        activateTopic,
        deactivateTopic
    };

    return (
        <AppContext.Provider value={contextValue}>
            {children}
        </AppContext.Provider>
    );
};