// splash.js
import * as rive from '@rive-app/canvas';

export const initRive = (canvasElement) => {
  let r;

  // Funcin para ajustar el tamao del canvas al redimensionar la ventana o cambiar de resolucin
  function computeSize() {
    if (r) {
      r.resizeDrawingSurfaceToCanvas();
    }
  }

  // Ajusta el canvas cuando se cambia el tamao de la ventana
  window.addEventListener("resize", computeSize);

  // Ajusta el canvas si cambia la densidad de pxeles (por ejemplo, si cambias de monitor)
  window
    .matchMedia(`(resolution: ${window.devicePixelRatio}dppx)`)
    .addEventListener("change", computeSize);

  // Inicializa la animacin Rive
  r = new rive.Rive({
    src: "./bpo_signature.riv",
    stateMachines: "Animation 3:00", // nombre de la mquina de estados definida en Rive
    canvas: canvasElement,
    layout: new rive.Layout({
      fit: rive.Fit.Contain, //  CAMBIO: fit debe ser 'Contain', 'Cover', 'Fill', etc. No 'Layout'
      layoutScaleFactor: 2,
    }),
    autoplay: true,
    onLoad: () => {
      r.resizeDrawingSurfaceToCanvas(); // ajusta el canvas una vez cargado
    },
  });

  return r;
};
